<template>
  <div class="setting">
    <h5>Синхронизировать базу</h5>
    <div class="card">
      <div class="card-body p-2">
        <b-alert
            :show="dismissCountDown"
            variant="warning"
            @dismissed="dismissCountDown=0"
            @dismiss-count-down="countDownChanged"
            class="mb-2"
        >
          <p>Идет загрузка осталось примерно {{ dismissCountDown }} секунд...</p>
          <b-progress
              variant="warning"
              :max="dismissSecs"
              :value="dismissCountDown"
              height="4px"
          ></b-progress>
        </b-alert>
        <b-alert variant="success" :show="isSuccess" class="mb-2">Данные перенесены</b-alert>
        <b-alert variant="danger" :show="isOffline" class="mb-2">Подключения до интернета отсутствует, попробуйте
          позже.
        </b-alert>
        <b-button :disabled="dismissCountDown !== 0" variant="primary" class="w-100" @click="loadProduct">
          Загрузить
        </b-button>
      </div>
    </div>
    <h5>Параметры</h5>
    <div class="row mb-2">
      <div class="col">
        <label for="bx_p">Множетил белков</label>
        <input type="number" v-model="bx_param" step="0.1" class="form-control" id="bx_p" placeholder="множетил белков">
      </div>
      <div class="col">
        <label for="bj_p">Множетил жиров</label>
        <input type="number" v-model="bj_param" step="0.1" class="form-control" id="bj_p" placeholder="множетил жиров">
      </div>
    </div>
    <b-alert show variant="warning">Важно!!! Если не учитывать в ХЕ белки и жиры то значение должны быть 0 </b-alert>

<!--    <b-button variant="primary" class="w-100" @click="setProduct">-->
<!--      В базу-->
<!--    </b-button>-->
  </div>
</template>

<script>
export default {
  name: 'Setting',
  data() {
    return {
      isSync: false,
      isOffline: false,
      isSuccess: false,
      dismissSecs: 3,
      dismissCountDown: 0,
      product: [],
      category: [],
    }
  },
  computed: {
    isOnline() {
      return window.navigator.onLine;
    },
    bx_param: {
      get() {
        return this.$store.state.option.bx_param
      },
      set(value) {
        this.$store.commit('setOptionBx', value)
      }
    },
    bj_param: {
      get() {
        return this.$store.state.option.bj_param
      },
      set(value) {
        this.$store.commit('setOptionBj', value)
      }
    }
  },
  methods: {
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown
    },
    async createDBCategory() {
      await this.$pouch.bulkDocs(this.category, {}, 'category').then(() => {
      }).catch(function (err) {
        console.error(err);
      });
    },
    async createDBProduct() {
      console.log(this.product);
      await this.$pouch.bulkDocs(this.product).then(() => {
        this.isSync = false;
        this.isSuccess = true;
        this.dismissCountDown = 0;
      }).catch(function (err) {
        console.error(err);
      });
    },

    async loadProduct() {
      this.dismissCountDown = this.dismissSecs
      this.isSync = true;
      this.product = [];
      this.category = [];
      if(!this.isOnline) {
        this.dismissCountDown = 0;
        this.isOffline = true;
        return;
      }
      await this.$firebase.database().ref('category').once('value').then((snapshot) => {
        snapshot.forEach((code_snapshot) => {
          this.category.push(code_snapshot.val());
        })
        this.createDBCategory();
      });
      await this.$firebase.database().ref('product').once('value').then((snapshot) => {
        snapshot.forEach((code_snapshot) => {
          this.product.push(code_snapshot.val());
        })
        this.createDBProduct();
      });
    },

    async setProduct() {
      try {
        await this.$pouch.allDocs({
          include_docs: true,
          attachments: true
        }).then((res) => {
          setTimeout(() => {
            res.rows.forEach(val => {
              delete val.doc._rev
              this.$firebase.database().ref().child(`product/${val.id}`).update(val.doc)
            })
          }, 1000)
         console.log(res.rows);
        });
      } catch (error) {
        console.error(error)
      }
    }
  },
  mounted() {
  }
}
</script>

<style scoped lang="scss">
.setting {
}
</style>
